import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Navigate } from 'react-router-dom';
import '../App.css';
import Row from '../components/row';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

function Cabinet({base_url, isLogin, user}) { 
  const [history, sethistory] = useState([])
  useEffect(() => {
    getHistory()
  }, [])
  const getHistory = async()=>{
    const history = await axios.get(`${base_url}api/applications/`, {withCredentials: true})
    sethistory(history.data);
  }
  console.log(user)
  return (
    <>
    {!isLogin && <Navigate replace to={'/'} />}
    <div className='cabinet-header'>
      <h1>ЛИЧНЫЙ КАБИНЕТ</h1>
    </div>
    <TableContainer sx={{width:'80%', maxHeight:700, margin: 'auto', background:'#2A2833'}} component={Paper}>
      <Table size='medium' aria-label="collapsible table">
        <TableHead>
          <TableRow >
            <TableCell />
            <TableCell id='table-items'>Тип сделки</TableCell>
            <TableCell id='table-items' align="left">Дата</TableCell>
            <TableCell id='table-items' align="right">Сумма</TableCell>
            <TableCell id='table-items' align="right">Реквизиты</TableCell>
            <TableCell id='table-items' align="right">Статус</TableCell>
            {user.is_admin && 
            <TableCell id='table-items' align="right">Действие</TableCell>}
          </TableRow>
        </TableHead>
        <TableBody>
          {history.map((item) => (
            <Row key={item.id} item={item} user={user} base_url={base_url}/>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </>
  );
}

export default Cabinet;